export default class AppView {
  constructor(titles) {
    this.titles = titles;
  }

  render() {
    const content = document.querySelectorAll('.slider')[0];
    // eslint-disable-next-line array-callback-return
    this.titles.map((titles) => {
      content.innerHTML += `
      <li class = "clip">
        <div class ="block">
          <div class = "first_block">
            <img src="${titles.snippet.thumbnails.default.url}" alt="альтернативный текст">
            <div class = "link">
              <a href='https://www.youtube.com/watch?v=${titles.id}' target="_blank">${titles.snippet.title}</a>
            </div>
          </div>
          <div class = "second_block">
            <table>
              <tr>
                <td><i class="fas fa-user"></i></td>
                <td>${titles.snippet.channelTitle}</td>
              </tr>
              <tr>
                <td><i class="fas fa-calendar-alt"></i></td>
                <td>${(titles.snippet.publishedAt).slice(0, 10)}</td>
              </tr>
              <tr>
                <td><i class="fas fa-eye"></i></td>
                <td>${titles.statistics.viewCount}</td>
              </tr>
            </table>
            <div class = 'title'>${titles.snippet.description}

            </div>
          </div>
        </div>
      </li>
      `;
    }).join('');
  }

  // eslint-disable-next-line class-methods-use-this
  getClassClips() {
    let parent;
    const slider = document.querySelectorAll('.slider')[0];
    slider.addEventListener('mouseover', (event) => {
      parent = event.target;
      if (parent.classList[0] !== 'slider') {
        while (parent.classList[0] !== 'clip') {
          parent = parent.parentNode;
        }
        parent.classList.add('mouseover');
      }
    });
    slider.addEventListener('mouseout', () => {
      parent.classList.remove('mouseover');
    });
  }
}
