/* eslint-disable radix */
import AppModel from '../models/AppModel';
import AppView from '../views/AppView';
import Search from '../views/Search';
import Buttons from '../views/Buttons';

export default class App {
  constructor(keyWord, newData) {
    this.state = {
      url: `https://www.googleapis.com/youtube/v3/search?key=AIzaSyAwwUKFasxlEbHIhp_gsfIC2PxDFfiWsno&type=video&part=snippet&maxResults=15&q=${keyWord}`,
      url2: `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyAwwUKFasxlEbHIhp_gsfIC2PxDFfiWsno&id=${newData}&part=snippet,statistics`,
    };
  }
  // AIzaSyDTJLYXegh_th_D_p4J65zOKUCiz4GnW98 - First key
  // AIzaSyAwwUKFasxlEbHIhp_gsfIC2PxDFfiWsno - Second key
  // AIzaSyCSRzA1_UMOQ58dspapqJ5pYwe077C8nOM

  // AIzaSyBzge8ZC9qyqiYCS7BsOHGyOb3WvbITK6I

  async start() {
    const search = new Search();
    await search.render();
    const stringSearch = document.querySelectorAll('.stringSearch')[0];
    const slider = document.querySelectorAll('.slider')[0];
    const sliderButtons = document.querySelectorAll('.sliderButtons')[0];
    const SliderStart = document.querySelector('.slider');
    const SliderStartWidth = parseInt(window.getComputedStyle(SliderStart).width);
    const debounce = (fn, time) => {
      let timeout;
      return function func() {
        const functionCall = () => fn.apply(this);
        clearTimeout(timeout);
        timeout = setTimeout(functionCall, time);
      };
    };
    stringSearch.addEventListener('input', debounce(async () => {
      // setTimeout(async () => {
      this.keyWord = stringSearch.value;
      slider.style.width = `${SliderStartWidth}px`;
      slider.style.left = 0;
      slider.innerHTML = '';
      sliderButtons.innerHTML = '';
      this.state.url = `https://www.googleapis.com/youtube/v3/search?key=AIzaSyAwwUKFasxlEbHIhp_gsfIC2PxDFfiWsno&type=video&part=snippet&maxResults=15&q=${this.keyWord}`;
      const model = new AppModel(this.state);
      const data = await model.getData();
      const newData = data.items.map(item => item.id.videoId).join(',');
      this.state.url2 = `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyAwwUKFasxlEbHIhp_gsfIC2PxDFfiWsno&id=${newData}&part=snippet,statistics`;
      const modelVideo = new AppModel(this.state);
      const dataVideo = await modelVideo.getDataVideo();
      const view = new AppView(dataVideo);
      slider.innerHTML = '';
      sliderButtons.innerHTML = '';
      view.render();
      view.getClassClips();
      const buttons = new Buttons();
      buttons.render();
      buttons.buttonMouseover();
      buttons.buttonClick();
      // }, 3000);
    // });
    }, 1000), false);
  }
}
