/* eslint-disable no-shadow */
/* eslint-disable radix */
import AppModel from '../../models/AppModel';
import AppView from '../AppView';

export default class Buttons {
  constructor(keyWord, newData) {
    this.state = {
      url: `https://www.googleapis.com/youtube/v3/search?key=AIzaSyAwwUKFasxlEbHIhp_gsfIC2PxDFfiWsno&type=video&part=snippet&maxResults=15&q=${keyWord}`,
      url2: `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyAwwUKFasxlEbHIhp_gsfIC2PxDFfiWsno&id=${newData}&part=snippet,statistics`,
    };
  }

  // eslint-disable-next-line class-methods-use-this
  render() {
    const sliderButtons = document.querySelectorAll('.sliderButtons')[0];
    const containerButtons = document.createElement('div');
    containerButtons.className = 'containerButtons';
    sliderButtons.appendChild(containerButtons);
    const hint1 = document.createElement('div');
    hint1.className = 'hint1';
    containerButtons.appendChild(hint1);
    // creat button1 and i1
    const button1 = document.createElement('button');
    button1.className = 'button one';
    button1.id = 'one';
    containerButtons.appendChild(button1);
    const i1 = document.createElement('i');
    button1.appendChild(i1);
    i1.className = 'fas fa-home';
    // creat button2 and i2
    const button2 = document.createElement('button');
    button2.className = 'button two';
    button2.id = 'two';
    containerButtons.appendChild(button2);
    const i2 = document.createElement('i');
    button2.appendChild(i2);
    i2.className = 'fas fa-chevron-left';
    // creat button3
    const button3 = document.createElement('button');
    button3.className = 'button three';
    button3.id = 'three';
    button3.innerHTML = '1';
    containerButtons.appendChild(button3);
    // creat button4 and i4
    const button4 = document.createElement('button');
    button4.className = 'button four';
    button4.id = 'four';
    containerButtons.appendChild(button4);
    const i4 = document.createElement('i');
    button4.appendChild(i4);
    i4.className = 'fas fa-chevron-right';
    const hint2 = document.createElement('div');
    hint2.className = 'hint2';
    containerButtons.appendChild(hint2);
  }

  // eslint-disable-next-line class-methods-use-this
  buttonMouseover() {
    const containerButtons = document.querySelectorAll('.containerButtons')[0];
    containerButtons.addEventListener('mouseover', (event) => {
      if (event.target.classList[0] === 'button') {
        event.target.classList.add('btnHover');
      }
      if (event.target.classList[0] === 'fas') {
        event.target.parentNode.classList.add('btnHover');
      }
      containerButtons.addEventListener('mouseout', () => {
        event.target.classList.remove('btnHover');
        event.target.parentNode.classList.remove('btnHover');
      });
    });
  }

  buttonClick() {
    const stringSearch = document.querySelectorAll('.stringSearch')[0];
    this.keyWord = stringSearch.value;
    // this.state.url = `https://www.googleapis.com/youtube/v3/search?key=AIzaSyAwwUKFasxlEbHIhp_gsfIC2PxDFfiWsno&type=video&part=snippet&maxResults=15&q=${this.keyWord}`;
    const btnOne = document.querySelectorAll('.one')[0];
    const btnTwo = document.getElementById('two');
    const btnThree = document.querySelectorAll('.three')[0];
    const btnFour = document.getElementById('four');
    const main = document.querySelectorAll('.main')[0];
    const SliderStart = document.querySelector('.slider');
    const SliderStartWidth = parseInt(window.getComputedStyle(SliderStart).width);
    const hint2 = document.querySelectorAll('.hint2')[0];
    const hint1 = document.querySelectorAll('.hint1')[0];
    btnFour.addEventListener('mousedown', () => {
      hint2.classList.add('hint-display');
      let num = +btnThree.innerHTML;
      num += 1;
      hint2.innerHTML = num;
    });
    document.addEventListener('mouseup', () => {
      hint2.classList.remove('hint-display');
    });
    btnTwo.addEventListener('mousedown', () => {
      if (btnThree.innerHTML !== '1') {
        hint1.classList.add('hint-display');
        let num = +btnThree.innerHTML;
        num -= 1;
        hint1.innerHTML = num;
      }
    });
    document.addEventListener('mouseup', () => {
      hint1.classList.remove('hint-display');
    });
    // Click on 4th button
    //
    btnFour.addEventListener('click', async () => {
      const slider = document.querySelectorAll('.slider')[0];
      const left = parseInt(window.getComputedStyle(slider).left);
      const widthSlider = parseInt(window.getComputedStyle(slider).width);
      const widthMain = parseInt(window.getComputedStyle(main).width);
      let num = +btnThree.innerHTML;
      num += 1;
      btnThree.innerHTML = num;
      if ((parseInt(slider.style.left) * (-1) < widthSlider)
        && (parseInt(slider.style.left) * (-1) > (widthSlider - 2 * widthMain))) {
        slider.style.width = `${widthSlider + SliderStartWidth}px`;
        const modelPageOne = new AppModel(this.state);
        const dataPage = await modelPageOne.getData();
        this.state.url = `https://www.googleapis.com/youtube/v3/search?pageToken=${dataPage.nextPageToken}&key=AIzaSyAwwUKFasxlEbHIhp_gsfIC2PxDFfiWsno&type=video&part=snippet&maxResults=15&q=${this.keyWord}`;
        const model = new AppModel(this.state);
        const data = await model.getData();
        const newData = data.items.map(item => item.id.videoId).join(',');
        this.state.url2 = `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyAwwUKFasxlEbHIhp_gsfIC2PxDFfiWsno&id=${newData}&part=snippet,statistics`;
        const modelVideo = new AppModel(this.state);
        const dataVideo = await modelVideo.getDataVideo();
        const view = new AppView(dataVideo);
        view.render();
        view.getClassClips();
        slider.style.left = `${left - widthMain}px`;
      } else {
        slider.style.left = `${left - widthMain}px`;
      }
    });
    //
    const slider = document.querySelectorAll('.slider')[0];
    const widthMain = parseInt(window.getComputedStyle(main).width);
    let end;
    //
    // mousedown
    //
    let start;
    let leftStart;
    slider.addEventListener('mousedown', (e) => {
      const clip = document.querySelectorAll('.clip');
      clip.forEach(item => item.classList.remove('mouseover'));
      slider.classList.add('no-transition');
      start = e.screenX;
      const left = parseInt(window.getComputedStyle(slider).left);
      leftStart = left;

      // leftStart = left;
      // document.addEventListener('mousemove', (e) => {
      // eslint-disable-next-line func-names
      document.onmousemove = function (e) {
        slider.style.left = `-${Math.abs(left) + (start - e.screenX) * 1.3}px`;
        end = e.screenX;
        if (start > end) {
          hint2.classList.add('hint-display');
          let num = +btnThree.innerHTML;
          num += 1;
          hint2.innerHTML = num;
        } else if (start < end && btnThree.innerHTML !== '1') {
          hint1.classList.add('hint-display');
          let num = +btnThree.innerHTML;
          num -= 1;
          hint1.innerHTML = num;
        }
      };
      //
      // move
      //
    });
    slider.addEventListener('mouseup', async () => {
      const slider = document.querySelectorAll('.slider')[0];
      slider.classList.remove('no-transition');
      document.onmousemove = null;
      const left = parseInt(window.getComputedStyle(slider).left);
      // document.onmouseup = async () => {
      if (start > end && start !== end) {
        const widthMain = parseInt(window.getComputedStyle(main).width);
        slider.style.left = `-${Math.abs(leftStart) + widthMain}px`;
        const btnThree = document.querySelectorAll('.three')[0];
        const widthSlider = parseInt(window.getComputedStyle(slider).width);

        //
        // Check the condition: is the slider offset almost equal to the slider width?
        //
        document.onmouseup = null;
        if (left * (-1) > (widthSlider - 3 * widthMain)) {
          slider.style.width = `${widthSlider + SliderStartWidth}px`;
          const modelPageOne = new AppModel(this.state);
          const dataPage = await modelPageOne.getData();
          this.state.url = `https://www.googleapis.com/youtube/v3/search?pageToken=${dataPage.nextPageToken}&key=AIzaSyAwwUKFasxlEbHIhp_gsfIC2PxDFfiWsno&type=video&part=snippet&maxResults=15&q=${this.keyWord}`;
          const model = new AppModel(this.state);
          const data = await model.getData();
          const newData = data.items.map(item => item.id.videoId).join(',');
          this.state.url2 = `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyAwwUKFasxlEbHIhp_gsfIC2PxDFfiWsno&id=${newData}&part=snippet,statistics`;
          const modelVideo = new AppModel(this.state);
          const dataVideo = await modelVideo.getDataVideo();
          const view = new AppView(dataVideo);
          view.render();
          view.getClassClips();
        }

        let num = +btnThree.innerHTML;
        num += 1;
        btnThree.innerHTML = num;
      }
      if (start < end && start !== end) {
        slider.style.left = `-${Math.abs(leftStart) - widthMain}px`;
        if (left === 0) btnThree.innerHTML = 1;
        else {
          let num = +btnThree.innerHTML;
          num -= 1;
          btnThree.innerHTML = num;
        }
      }
    });
    // eslint-disable-next-line func-names
    slider.ondragstart = function () {
      return false;
    };
    btnTwo.addEventListener('click', async () => {
      const slider = document.querySelectorAll('.slider')[0];
      const left = parseInt(window.getComputedStyle(slider).left);
      const widthMain = parseInt(window.getComputedStyle(main).width);
      if (left === 0 || Math.abs(left) < widthMain * 1.5) {
        slider.style.left = 0;
        btnThree.innerHTML = 1;
      } else {
        slider.style.left = `${left + widthMain}px`;
        let num = +btnThree.innerHTML;
        num -= 1;
        btnThree.innerHTML = num;
      }
    });
    btnOne.addEventListener('click', async () => {
      const slider = document.querySelectorAll('.slider')[0];
      slider.style.left = 0;
      btnThree.innerHTML = 1;
    });

    // for mobile

    let end1;
    //
    // mousedown
    //
    let start1;
    let leftStart1;
    slider.addEventListener('touchstart', (e) => {
      const clip = document.querySelectorAll('.clip');
      clip.forEach(item => item.classList.remove('mouseover'));
      slider.classList.add('no-transition');
      start1 = e.changedTouches[0].clientX;
      const left = parseInt(window.getComputedStyle(slider).left);
      leftStart1 = left;

      // leftStart = left;
      // document.addEventListener('mousemove', (e) => {
      // eslint-disable-next-line func-names
      document.ontouchmove = function (e) {
        slider.style.left = `-${Math.abs(left) + (start1 - e.changedTouches[0].clientX) * 1.3}px`;
        end1 = e.changedTouches[0].clientX;
        if (start1 > end1) {
          hint2.classList.add('hint-display');
          let num = +btnThree.innerHTML;
          num += 1;
          hint2.innerHTML = num;
        } else if (start1 < end1 && btnThree.innerHTML !== '1') {
          hint1.classList.add('hint-display');
          let num = +btnThree.innerHTML;
          num -= 1;
          hint1.innerHTML = num;
        }
      };
      //
      // move
      //
    });
    slider.addEventListener('touchend', async () => {
      hint1.classList.remove('hint-display');
      hint2.classList.remove('hint-display');
      const slider = document.querySelectorAll('.slider')[0];
      slider.classList.remove('no-transition');
      document.ontouchmove = null;
      const left = parseInt(window.getComputedStyle(slider).left);
      // document.onmouseup = async () => {
      if (start1 > end1 && start1 !== end1) {
        const widthMain = parseInt(window.getComputedStyle(main).width);
        slider.style.left = `-${Math.abs(leftStart1) + widthMain}px`;
        const btnThree = document.querySelectorAll('.three')[0];
        const widthSlider = parseInt(window.getComputedStyle(slider).width);

        //
        // Check the condition: is the slider offset almost equal to the slider width?
        //
        document.ontouchend = null;
        if (left * (-1) > (widthSlider - 3 * widthMain)) {
          slider.style.width = `${widthSlider + SliderStartWidth}px`;
          const modelPageOne = new AppModel(this.state);
          const dataPage = await modelPageOne.getData();
          this.state.url = `https://www.googleapis.com/youtube/v3/search?pageToken=${dataPage.nextPageToken}&key=AIzaSyAwwUKFasxlEbHIhp_gsfIC2PxDFfiWsno&type=video&part=snippet&maxResults=15&q=${this.keyWord}`;
          const model = new AppModel(this.state);
          const data = await model.getData();
          const newData = data.items.map(item => item.id.videoId).join(',');
          this.state.url2 = `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyAwwUKFasxlEbHIhp_gsfIC2PxDFfiWsno&id=${newData}&part=snippet,statistics`;
          const modelVideo = new AppModel(this.state);
          const dataVideo = await modelVideo.getDataVideo();
          const view = new AppView(dataVideo);
          view.render();
          view.getClassClips();
        }

        let num = +btnThree.innerHTML;
        num += 1;
        btnThree.innerHTML = num;
      }
      if (start1 < end1 && start1 !== end1) {
        slider.style.left = `-${Math.abs(leftStart1) - widthMain}px`;
        if (left === 0) btnThree.innerHTML = 1;
        else {
          let num = +btnThree.innerHTML;
          num -= 1;
          btnThree.innerHTML = num;
        }
      }
    });
    // eslint-disable-next-line func-names
    slider.ondragstart = function () {
      return false;
    };
  }
}
