export default class AppModel {
  constructor(state) {
    this.state = state;
  }

  static extractgetData(data) {
    return data;
  }

  async getData() {
    const { url } = this.state;
    const responce = await fetch(url);
    const data = await responce.json();
    return AppModel.extractgetData(data);
  }

  static getDataVideo(newData) {
    return newData.items;
  }

  async getDataVideo() {
    const { url2 } = this.state;
    const responce = await fetch(url2);
    const newData = await responce.json();
    return AppModel.getDataVideo(newData);
  }
}
