export default class Search {
  // eslint-disable-next-line class-methods-use-this
  render() {
    const link = document.createElement('link');
    document.head.appendChild(link);
    link.rel = 'stylesheet';
    link.href = 'https://use.fontawesome.com/releases/v5.7.2/css/all.css';
    link.integrity = 'sha384-fnmOCqbTlWIlj8LyTjo7mOUStjsKC4pOpQbqyi7RrhN7udi9RwhKkMHpvLbHG9Sr';
    link.crossorigin = 'anonymous';
    const header = document.createElement('header');
    header.className = 'header';
    document.body.appendChild(header);
    const i = document.createElement('i');
    header.appendChild(i);
    i.className = 'fas fa-search';
    const input = document.createElement('input');
    input.type = 'text';
    input.className = 'stringSearch';
    input.placeholder = 'What do you want to find?';
    header.appendChild(input);
    // const btn = document.createElement('button');
    // btn.className = 'btn';
    // header.appendChild(btn);
    const main = document.createElement('main');
    main.className = 'main';
    document.body.appendChild(main);
    const content = document.createElement('ul');
    content.className = 'slider';
    main.appendChild(content);
    const sliderButtons = document.createElement('div');
    sliderButtons.className = 'sliderButtons';
    main.appendChild(sliderButtons);
  }
}
